
import 'mavon-editor/dist/css/index.css'
import {mavonEditor} from 'mavon-editor'
import XInput from 'vux/src/components/x-input/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import PopupHeader from 'vux/src/components/popup-header/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Radio from 'vux/src/components/radio/index.vue'

import axios from 'axios'
import { getItem } from '../../utils/storage'
import { fileInfoTran } from '../../utils/dataTran'

export default {
data () {
return {
item: {},
options: [{
key: 0,
value: ''
}, {
key: 1,
value: ''
}],
show: false,
opt: 0,
userId: getItem('user').id
}
},
components: {
mavonEditor,
XInput,
XHeader,
PopupHeader,
Popup,
Group,
Radio
},
mounted () {
const { item } = this.$route.params
this.item = item || {
title: '',
content: '',
type: 0,
fileType: 'md',
fileId: ''
}
console.log(this.item)
},
methods: {
save () {
this.item.fileId === '' ? this.show = true : this.saveFile()
},
saveFile () {
this.show = false
const body = {
id: this.userId,
title: this.title === '' ? '' : this.title,
...this.item
}
axios
.post(`/api/savefile`, body)
.then((res) => {

const opt = {
name: 'noteShow',
params: {
item: fileInfoTran([res.data.fileInfo], true, res.data.isDiff)
}
}
this.$router.push(opt)
}).catch((error) => {
console.log(error)
})
}
}
}



