
import Masker from 'vux/src/components/masker/index.vue'


export default {
data () {
return {
list: [{
title: '',
desc: '',
target: 'input',
isInput: false,
img: require('@/assets/note-add-input.jpg')
}, {
title: 'MarkDown',
desc: 'markdown',
target: 'markdown',
isInput: false,
img: require('@/assets/note-add-input.jpg')
}, {
title: '',
desc: '',
target: 'voice',
isInput: false,
img: require('@/assets/note-add-input.jpg')
}, {
title: 'aaaa',
desc: '',
target: 'scan',
isInput: true,
img: require('@/assets/note-add-scan.jpg')
}, {
title: '',
desc: '',
target: 'uploadImg',
isInput: true,
img: require('@/assets/note-add-upload.png')
}]
}
},
methods: {
onClickMask (item) {
!item.isInput && this.$router.push(`/note/add/${item.target}`)
if (item.isInput && item.target === 'scan') {
console.log(this.$refs.photo)
this.$refs.photo.dispatchEvent(new MouseEvent('click'))
} else if (item.isInput && item.target === 'uploadImg') {
console.log(this.$refs)
this.$refs.img.click()
}
}
},
components: {
Masker
}
}



