
import AlertModule from 'vux/src/plugins/alert/module'
import Search from 'vux/src/components/search/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import PopupHeader from 'vux/src/components/popup-header/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Radio from 'vux/src/components/radio/index.vue'
import Group from 'vux/src/components/group/index.vue'

import axios from 'axios'
import { fileInfoTran } from '../../utils/dataTran'
import { getItem } from '../../utils/storage'
export default {
components: {
Search,
Swipeout,
SwipeoutItem,
SwipeoutButton,
Tab,
TabItem,
Confirm,
AlertModule,
PopupHeader,
Popup,
Radio,
Group
},
data () {
return {
value: '',
list: [],
index01: 0,
show: false,
currentIndex: 0,
confirmCont: '',
currentTabIndex: 0,
show1: false,
options: [{
key: 0,
value: ''
}],
shareType: '',
userId: getItem('user').id
}
},

mounted () {
this.getFileList(0, 0)
},
methods: {
getResult (val) {
this.getFileList(this.currentTabIndex, this.currentTabIndex === 2 ? 1 : 0, val)
},
change (value, label) {
this.shareType = value
},
dele () {
const item = this.list[this.currentIndex]
const body = {
userId: this.userId,
fileId: item.id
}
const url = item.isShare ? '/api/delsharefile' : '/api/deletefile'
axios.post(url, body)
.then((res) => {
!item.isShare && AlertModule.show({
title: '',
content: this.list[this.currentIndex].isTrash ? '' : ''
})
this.getFileList(this.currentTabIndex, this.currentTabIndex === 2 ? 1 : 0)
}).catch((error) => {
console.log(error)
})
},
share () {

const re = /^\w+((-\w+)|(\.\w+))*\@[A-Za-z0-9]+((\.|-)[A-Za-z0-9]+)*\.[A-Za-z0-9]+$/
if (this.shareType === 0) {
this.$router.push({
name: 'choosefriend',
params: {
fileId: this.list[this.currentIndex].fileId
}
})
} else if (re.test(this.shareType) === true) {
console.log('')
this.readySendMail(this.shareType)
} else {
AlertModule.show({
title: ''
})
this.show1 = true
}
},
readySendMail (email) {

axios
.post(`/api/sendmail`, {
from: getItem('user').name,
to: email,
content: this.list[this.currentIndex].content
})
.then((res) => {
this.$vux.toast.text('')
this.show1 = false
}).catch((error) => {
this.$vux.toast.text(error || '')
})
},
revert (index) {
const body = {
userId: this.userId,
fileId: this.list[this.currentIndex].fileId
}
axios.post(`/api/revertfile`, body)
.then((res) => {
this.getFileList(this.currentTabIndex, this.currentTabIndex === 2 ? 1 : 0)
}).catch((error) => {
console.log(error)
})
},
onSubmit () {
this.$refs.search.setBlur()
this.$vux.toast.show({
type: 'text',
position: 'top',
text: 'on submit'
})
},
switchTabitem (index) {
this.currentTabIndex = index
switch (index) {
case 0: this.getFileList(0, 0)
break
case 1: this.getFileList(1, 0)
break
case 2: this.getFileList(2, 0)
break
case 3: this.getFileList(3, 1)
break
default: this.getFileList(0, 0)
break
}
},
jumpToShow (index) {
const data = this.list[index]
const opt = data.fileType === 'md' ? {
name: 'noteAddMakdown',
params: {
item: data
}
} : {
name: 'noteShow',
params: {
item: data
}
}
this.$router.push(opt)
},
getFileList (type, isTrash, val) {
axios.get(`/api/getfilelist`, {
params: {
id: this.userId,
type,
isTrash,
val: val || ''
}})
.then((res) => {
if (res.data.status) {
this.list = fileInfoTran(res.data.list)
} else {
AlertModule.show({
content: res.data.msg
})
}
}).catch((error) => {
console.log(error)
})
}
}
}



