
import Vue from 'vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import PopupHeader from 'vux/src/components/popup-header/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Radio from 'vux/src/components/radio/index.vue'

import VueHtml5Editor from 'vue-html5-editor'
import axios from 'axios'
import 'font-awesome/css/font-awesome.min.css'
import { getItem } from '../../utils/storage'

export default {
name: 'h5Editor',
data () {
return {
item: {},
options: [{
key: 0,
value: ''
}, {
key: 1,
value: ''
}],
show: false,
opt: 0,
userId: getItem('user').id
}
},
components: {
XInput,
XHeader,
PopupHeader,
Popup,
Group,
Radio
},
mounted () {
const { item } = this.$route.params
this.item = item || {
title: '',
content: '',
type: 0,
fileType: 'html',
fileId: ''
}
console.log(this.item)
},
methods: {
updateData (e = '') {
this.item.content = e
},
saveFile () {
this.show = !this.show
console.log(this.item)
const body = {
id: this.userId,
title: this.title === '' ? '' : this.title,
...this.item,
type: this.opt
}
axios
.post(`/api/savefile`, body)
.then((res) => {
res.data && this.$router.push({
name: 'noteList'
})
}).catch((error) => {
console.log(error)
})
}
}
}
Vue.use(VueHtml5Editor, {

name: 'vue-html5-editor',


showModuleName: false,


icons: {
text: 'fa fa-pencil',
color: 'fa fa-paint-brush',
font: 'fa fa-font',
align: 'fa fa-align-justify',
list: 'fa fa-list',
link: 'fa fa-chain',
unlink: 'fa fa-chain-broken',
tabulation: 'fa fa-table',
image: 'fa fa-file-image-o',
hr: 'fa fa-minus',
eraser: 'fa fa-eraser',
undo: 'fa-undo fa',
'full-screen': 'fa fa-arrows-alt',
info: 'fa fa-info'
},


image: {

sizeLimit: 512 * 1024,


upload: {
url: null,
headers: {},
params: {},
fieldName: {}
},



compress: {
width: 1600,
height: 1600,
quality: 80
},


uploadHandler (responseText) {

var json = JSON.parse(responseText)
if (!json.ok) {
alert(json.msg)
} else {
return json.data
}
}
},


language: 'zh-cn',

i18n: {

'zh-cn': {
'align': '',
'image': '',
'list': '',
'link': '',
'unlink': '',
'table': '',
'font': '',
'full screen': '',
'text': '',
'eraser': '',
'info': '',
'color': '',
'please enter a url': '',
'create link': '',
'bold': '',
'italic': '',
'underline': '',
'strike through': '',
'subscript': '',
'superscript': '',
'heading': '',
'font name': '',
'font size': '',
'left justify': '',
'center justify': '',
'right justify': '',
'ordered list': '',
'unordered list': '',
'fore color': '',
'background color': '',
'row count': '',
'column count': '',
'save': '',
'upload': '',
'progress': '',
'unknown': '',
'please wait': '',
'error': '',
'abort': '',
'reset': ''
}
},


hiddenModules: [],



visibleModules: [
'text',
'color',
'font',
'align',
'list',
'link',
'unlink',
'tabulation',
'image',
'hr',
'eraser',
'undo',
'full-screen',
'info'
],


modules: {

}
})



