
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import InlineCalendar from 'vux/src/components/inline-calendar/index.vue'

import 'font-awesome/css/font-awesome.min.css'
export default {
components: {
Group,
Cell,
CellBox,
XHeader,
InlineCalendar
},
data () {
return {
item: {},
diffRes: []
}
},
mounted () {
this.item = this.$route.params.item
if (this.item.fileType === 'html') {
setTimeout(() => {
this.$refs.htmlContent.innerHTML = this.item.content
}, 50)
}
this.diffRes = this.item.content
},
methods: {
editFile () {
let name = ''
switch (this.item.fileType) {
case 'md': name = 'noteAddMakdown'
break
case 'html': name = 'noteAddInput'
break
default: name = 'noteAddMakdown'
break
}
const opt = {
name,
params: {
item: this.item
}
}
this.$router.push(opt)
}
}
}



